.page {
  font-family: Cinzel, serif;
  color: $text-color;
  scroll-behavior: smooth;
  
  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform $transition-time;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 100px;

    @include onDesktop {
      margin-bottom: 140px;
    }
  }

  &__section-title {
    margin: 0;

    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $text-color;

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }
}
